import React from "react";

class SideNavSection extends React.Component {
    render() {
        return (
            <div className='mt-16'>
                {this.props.sectionTitle !== undefined && (
                    <p className='text-gray-200 mt-6 mb-3 pl-3 font-bold'>{this.props.sectionTitle}</p>
                )}
                {this.props.children}
            </div>
        )
    }
}

export default SideNavSection