import React from "react";

class sideNavMenuList extends React.Component {
    render() {
        return (
            <div className='flex items-center justify-between py-3 px-3 rounded-lg transition-all hover:bg-red-700'>
                <div className='flex'>
                    <p className='text-gray-300 text-lg mr-3'>{ this.props.menuIcon }</p>
                    <p className='text-gray-300 text-sm'>{this.props.menuList}</p>
                </div>

                {this.props.notification !== undefined && (
                    <div>
                        <p className='bg-gray-300 text-xs font-bold text-gray-700 p-1 rounded-full text-center' style={{minWidth: 25, height: 25}}>{this.props.notification}</p>
                    </div>
                )}
            </div>
        )
    }
}

export default sideNavMenuList;